// Angular Dependencies
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule } from '@angular/router';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
// Router
import { FormsRoutes } from './forms.routing';

// Core Pages Layout Components
import { SharedModule } from '../@pages/components/shared.module';

// Bootstrap Components by ngx-bootstrap
import { TabsModule } from 'ngx-bootstrap';
import { ButtonsModule } from 'ngx-bootstrap/buttons';
import { TypeaheadModule } from 'ngx-bootstrap';
import { AlertModule } from 'ngx-bootstrap';
import { ModalModule } from 'ngx-bootstrap';
import { BsDropdownModule } from 'ngx-bootstrap';

// Thirdparty Treeview component
import { TreeModule } from 'angular-tree-component';

// Pages Components by ace
import { pgSelectModule } from '../@pages/components/select/select.module';
import { pgTagModule } from '../@pages/components/tag/tag.module';
import { pgSwitchModule } from '../@pages/components/switch/switch.module';
import { pgTimePickerModule } from '../@pages/components/time-picker/timepicker.module';
import { pgTabsModule } from '../@pages/components/tabs/tabs.module';
import { pgSelectfx } from '../@pages/components/cs-select/select.module';
import { pgDatePickerModule } from '../@pages/components/datepicker/datepicker.module';
import { pgUploadModule } from '../@pages/components/upload/upload.module';

// Thirdparty components
import { TextMaskModule } from 'angular2-text-mask';
import { QuillModule } from 'ngx-quill'

// Sample Demo pages
import { FormWizardComponent } from './form-wizard/form-wizard.component';
import { FormElementsComponent } from './form-elements/form-elements.component';
import { FormLayoutsComponent } from './form-layouts/form-layouts.component';
import { TypopageComponent } from '.pages/ui/typopage/typopage.component';
import { ColorspageComponent } from '.pages/ui/colorspage/colorspage.component';
import { ButtonspageComponent } from '.pages/ui/buttonspage/buttonspage.component';
import { IconspageComponent } from '.pages/ui/iconspage/iconspage.component';
import { ModalspageComponent } from '.pages/ui/modalspage/modalspage.component';
import { TabspageComponent } from '.pages/ui/tabspage/tabspage.component';
import { NotificationspageComponent } from '.pages/ui/notificationspage/notificationspage.component';
import { TreeviewpageComponent } from '.pages/ui/treeviewpage/treeviewpage.component';
import { CardsComponentPage } from '.pages/cards/cards.component';
import { IconsService } from '.pages/ui/iconspage/icons.service';

// Required for Date Pickers
import { pgDatePickerComponent } from '.pages/@pages/components/datepicker/datepicker.component';
import { pgRangePickerComponent } from '.pages/@pages/components/datepicker/rangepicker.component';

// Required for Buttons
import { FilterPipeModule } from 'ngx-filter-pipe';
import { MessageService } from '.pages/@pages/components/message/message.service';
import { MessageModule } from '.pages/@pages/components/message/message.module';
import { ProgressModule } from '.pages/@pages/components/progress/progress.module';
import { pgCardModule } from '.pages/@pages/components/card/card.module';
import { pgCardSocialModule } from '.pages/@pages/components/card-social/card-social.module';
import { pgOptionComponent } from '.pages/@pages/components/select/option.component';
import { pgSelectComponent } from '.pages/@pages/components/select/select.component';

@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    RouterModule.forChild(FormsRoutes),
    FormsModule,
    ReactiveFormsModule,
    TabsModule.forRoot(),
    ButtonsModule.forRoot(),
    pgSelectModule,
    pgTagModule,
    TextMaskModule,
    pgSwitchModule,
    pgTimePickerModule,
    pgTabsModule,
    pgSelectfx,
    pgUploadModule,
    TypeaheadModule.forRoot(),
    pgDatePickerModule,
    QuillModule,
    BsDropdownModule,
    FilterPipeModule,
    AlertModule.forRoot(),
    ModalModule.forRoot(),
    MessageModule,
    TreeModule.forRoot(),
    ProgressModule,
    pgCardModule,
    pgCardSocialModule
  ],
  declarations: [
    FormWizardComponent,
    FormElementsComponent,
    FormLayoutsComponent,
    TypopageComponent,
    ColorspageComponent,
    ButtonspageComponent,
    IconspageComponent,
    NotificationspageComponent,
    ModalspageComponent,
    TabspageComponent,
    TreeviewpageComponent,
    CardsComponentPage,
  ],
  exports: [
    FormWizardComponent,
    FormElementsComponent,
    FormLayoutsComponent,
    TypopageComponent,
    ColorspageComponent,
    ButtonspageComponent,
    IconspageComponent,
    NotificationspageComponent,
    ModalspageComponent,
    TabspageComponent,
    TreeviewpageComponent,
    CardsComponentPage,
    pgDatePickerComponent,
    pgRangePickerComponent,
    pgOptionComponent,
    pgSelectComponent,
    pgRangePickerComponent
  ],
  providers: [
    IconsService,
    MessageService
  ]
})
export class FormsPageModule { }
